import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import useAttribute from '../../../hooks/profiles/useAttribute';
import { AttributeVm } from '../../../hooks/profiles/useAttributes';
import ActionButton from '../../Buttons/ActionButton';
import SaveStatus from '../../Buttons/SaveStatus';
import Section from '../../Sections/Section';
import AclEditor, { getAclSummary } from '../../AclEditor/AclEditor';
import AttributeFields from '../AttributeFields/AttributeFields';

const AttributeEditor = ({
  attribute,
  className,
  reorderAttr,
  onSave,
  doRemove,
}: {
  attribute: AttributeVm;
  className?: string;
  reorderAttr?: (attr: AttributeVm, dir: 1 | -1) => Promise<number | undefined>;
  onSave?: (attr: AttributeVm) => void;
  doRemove?: (attr: AttributeVm) => void;
}) => {
  const {
    save: { mutate: saveAttr, status, isLoading, isError, isSuccess },
    remove: { mutate: removeAttr },
  } = useAttribute({});
  const [newAttr, setNewAttr] = useState({ ...attribute });

  const saveNewData = (dirtyAttr) => {
    saveAttr(dirtyAttr);
  };

  const changeHandler = (e: { target: { value: unknown; name: string } }) => {
    const dirtyAttr = { ...newAttr };

    dirtyAttr.data[e.target.name] = e.target.value;

    setNewAttr(dirtyAttr);
    saveNewData(dirtyAttr);
    onSave(dirtyAttr);
  };

  const reorder = async (dir: 1 | -1) => {
    const newPriority = await reorderAttr(attribute, dir);
    if (!newPriority) {
      return;
    }
    const dirtyAttr = { ...newAttr, priority: newPriority };
    setNewAttr(dirtyAttr);
    saveNewData(dirtyAttr);
  };

  return (
    <>
      <Section
        isOpaqueBg={true}
        title={
          <>
            <span className="flex flex-row">
              <AclEditor
                className="mt-2 mb-auto mr-2"
                acl={newAttr.acl}
                onChange={(acl) => {
                  const dirtyAttr = { ...newAttr, acl: acl };

                  setNewAttr(dirtyAttr);
                  saveNewData(dirtyAttr);
                }}
              />{' '}
              <span>
                {getAclSummary(newAttr.acl)}{' '}
                <small className="block text-xs">{newAttr.typeDefinition.name}</small>
              </span>
            </span>
          </>
        }
        className={`${className} relative`}
      >
        <AttributeFields attribute={newAttr} onChange={changeHandler} />
        <div className="top-5 right-5 flex flex-row md:absolute">
          {reorderAttr && (
            <>
              <ActionButton
                className="ml-2"
                icon="up"
                type="secondary"
                onClick={() => reorder(-1)}
              />
              <ActionButton
                className="ml-2"
                icon="down"
                type="secondary"
                onClick={() => reorder(1)}
              />
            </>
          )}
          <ActionButton
            type="remove"
            icon="trash"
            className="ml-2"
            confirmOptions={{
              title: t('Remove Attribute'),
              buttonText: t('Permanently remove'),
              body: `${t('Are you sure you want to remove your')} ${
                attribute.typeDefinition.name
              } ${t('attribute. This action cannot be undone.')}`,
            }}
            onClick={() => (doRemove ? doRemove(attribute) : removeAttr(attribute))}
          ></ActionButton>
          <ActionButton
            state={isLoading ? 'loading' : isSuccess ? 'success' : isError ? 'error' : 'success'}
            type="primary"
            className="ml-2"
            onClick={() => saveNewData(newAttr)}
          >
            {t('Save')}
          </ActionButton>
        </div>
        <SaveStatus className="mt-2 text-right sm:mt-0" state={status} />
      </Section>
    </>
  );
};

export default AttributeEditor;
