import { AccessControlList, SecurityGroupType } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../../helpers/i18n/dictionary';
import usePortal from '../../../hooks/portal/usePortal';
import ActionButton from '../../Buttons/ActionButton';
import Radio from '../../Form/Radio';
import DialogWrapper from '../Common/DialogWrapper';

const AclDialog = ({
  title,
  confirmText,
  isOpen,
  acl,
  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;

  acl: AccessControlList;

  onConfirm: (acl: AccessControlList) => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');
  const [newAcl, setNewAcl] = useState<AccessControlList>(acl);

  if (!isOpen) {
    return null;
  }

  const saveAcl = () => {
    onConfirm(newAcl);
  };

  const updateSecurityGroup = (e) => {
    setNewAcl({ ...acl, requiredSecurityGroup: SecurityGroupType[e.target.value] });
  };

  const dialog = (
    <DialogWrapper title={title} onClose={onCancel}>
      <>
        <div>
          <Radio
            id="anonymous"
            value="Anonymous"
            name="securityGroup"
            checked={newAcl.requiredSecurityGroup === SecurityGroupType.Anonymous}
            onChange={updateSecurityGroup}
          />
          <label htmlFor="anonymous" className="ml-2">
            Anonymous
          </label>
        </div>
        <div>
          <Radio
            id="authenticated"
            value="Authenticated"
            name="securityGroup"
            checked={newAcl.requiredSecurityGroup === SecurityGroupType.Authenticated}
            onChange={updateSecurityGroup}
          />
          <label htmlFor="authenticated" className="ml-2">
            Authenticated
          </label>
        </div>
        <div>
          <Radio
            id="connected"
            value="Connected"
            name="securityGroup"
            checked={newAcl.requiredSecurityGroup === SecurityGroupType.Connected}
            onChange={updateSecurityGroup}
          />
          <label htmlFor="connected" className="ml-2">
            Connected
          </label>
        </div>
        <div>
          <Radio
            id="owner"
            value="Owner"
            name="securityGroup"
            checked={newAcl.requiredSecurityGroup === SecurityGroupType.Owner}
            onChange={updateSecurityGroup}
          />
          <label htmlFor="owner" className="ml-2">
            Owner
          </label>
        </div>

        <div className="-m-2 flex flex-row-reverse px-4 py-3">
          <ActionButton className="m-2" onClick={saveAcl}>
            {confirmText ?? 'Save'}
          </ActionButton>
          <ActionButton className="m-2" type="secondary" onClick={onCancel}>
            {t('Cancel')}
          </ActionButton>
        </div>
      </>
    </DialogWrapper>
  );

  return createPortal(dialog, target);
};

export default AclDialog;
