import {
  AttributeDefinitions,
  BuiltInProfiles,
  getTargetDriveFromProfileId,
  HomePageAttributes,
  HomePageConfig,
  HomePageTheme,
} from '@youfoundation/transit-lib';
import { debounce } from 'lodash-es';
import { useMemo } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import { AttributeVm } from '../../../hooks/profiles/useAttributes';
import ImageSelector from '../../Form/ImageSelector';
import Input from '../../Form/Input';
import Select from '../../Form/Select';
import Textarea from '../../Form/Textarea';

const AttributeFields = ({
  attribute,
  onChange,
}: {
  attribute: AttributeVm;
  onChange: React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>;
}) => {
  const debouncedChange = useMemo(() => debounce(onChange, 500), [onChange]);

  const tmpKeys = Object.keys(HomePageTheme).filter((f) => !isNaN(Number(f)));
  const tmpVals = Object.keys(HomePageTheme).filter((f) => isNaN(Number(f)));

  switch (attribute.type) {
    case AttributeDefinitions.Name.type.toString():
      return (
        <div className="-mx-2 flex flex-row">
          <div className="mb-5 w-2/5 px-2">
            <label htmlFor="given-name">{t('Given name')}</label>
            <Input
              id="given-name"
              name="givenName"
              defaultValue={attribute.data?.['givenName'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5 w-3/5 px-2">
            <label htmlFor="sur-name">{t('Surname')}</label>
            <Input
              id="sur-name"
              name="surname"
              defaultValue={attribute.data?.['surname'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </div>
      );
      break;
    case AttributeDefinitions.Photo.type.toString():
      return (
        <div className="mb-5">
          <label htmlFor="profileImageId">{t('Profile Image')}</label>
          <ImageSelector
            id="profileImageId"
            name="profileImageId"
            defaultValue={attribute.data?.['profileImageId'] ?? ''}
            onChange={debouncedChange}
            acl={attribute.acl}
            targetDrive={getTargetDriveFromProfileId(BuiltInProfiles.StandardProfileId.toString())}
          />
        </div>
      );
      break;
    case AttributeDefinitions.InstagramUsername.type.toString():
    case AttributeDefinitions.TiktokUsername.type.toString():
    case AttributeDefinitions.TwitterUsername.type.toString():
    case AttributeDefinitions.LinkedinUsername.type.toString():
    case AttributeDefinitions.FacebookUsername.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="handle">
              {attribute.typeDefinition.name} {t('Username')}
            </label>
            <Input
              id="handle"
              name={attribute.typeDefinition.name.toLowerCase()}
              defaultValue={attribute.data?.[attribute.typeDefinition.name.toLowerCase()] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.FullBio.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="short-bio">{t('Bio')}</label>
            <Input
              id="short-bio"
              name="short_bio"
              defaultValue={attribute.data?.['short_bio'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="full-bio">{t('Full bio')}</label>
            <Textarea
              id="full-bio"
              name="full_bio"
              defaultValue={attribute.data?.['full_bio'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.ShortBio.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="short-bio">{t('Bio')}</label>
            <Input
              id="short-bio"
              name="short_bio"
              defaultValue={attribute.data?.['short_bio'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.Link.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="link-text">{t('Text')}</label>
            <Input
              id="link-text"
              name="link_text"
              defaultValue={attribute.data?.['link_text'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="link-target">{t('Target')}</label>
            <Input
              id="link-target"
              name="link_target"
              defaultValue={attribute.data?.['link_target'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case AttributeDefinitions.CreditCard.type.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="cc-alias">{t('Alias')}</label>
            <Input
              id="cc-alias"
              name="cc_alias"
              defaultValue={attribute.data?.['cc_alias'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="cc-name">{t('Name on Card')}</label>
            <Input
              id="cc-name"
              name="cc_name"
              defaultValue={attribute.data?.['cc_name'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="cc-number">{t('Credit card number')}</label>
            <Input
              id="cc-number"
              name="cc_number"
              defaultValue={attribute.data?.['cc_number'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case HomePageAttributes.HomePage.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="headerImageUrl">{t('Header Image')}</label>
            <ImageSelector
              id="headerImageUrl"
              name="headerImageUrl"
              defaultValue={attribute.data?.['headerImageUrl'] ?? ''}
              onChange={debouncedChange}
              acl={attribute.acl}
              targetDrive={getTargetDriveFromProfileId(HomePageConfig.DefaultDriveId.toString())}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="tagLine">{t('Tag Line')}</label>
            <Input
              id="tagLine"
              name="tagLine"
              defaultValue={attribute.data?.['tagLine'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
          <div className="mb-5">
            <label htmlFor="leadText">{t('Lead Text')}</label>
            <Textarea
              id="leadText"
              name="leadText"
              defaultValue={attribute.data?.['leadText'] ?? ''}
              onChange={debouncedChange}
            />
          </div>
        </>
      );
      break;
    case HomePageAttributes.Theme.toString():
      return (
        <>
          <div className="mb-5">
            <label htmlFor="themeId">{t('Theme')}</label>
            <Select
              id="themeId"
              name="themeId"
              defaultValue={attribute.data?.['themeId'] ?? ''}
              onChange={debouncedChange}
            >
              <option>{t('Make a selection')}</option>
              {tmpKeys.map((tmpKey, index) => {
                return (
                  <option value={tmpKey} key={tmpKey}>
                    {t(tmpVals[index])}
                  </option>
                );
              })}
            </Select>
          </div>
        </>
      );
      break;
    default:
      return (
        <>
          {Object.keys(attribute.data).map((dataKey) => {
            return (
              <p className="whitespace-pre-line" key={dataKey}>
                {dataKey}: {attribute.data[dataKey]}
              </p>
            );
          })}
        </>
      );
  }
};

export default AttributeFields;
