import { SecurityGroupType } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import useAttributeOrderer, { attributeGroup } from '../../../hooks/profiles/useAttributeOrderer';
import { AttributeVm } from '../../../hooks/profiles/useAttributes';
import ActionButton from '../../Buttons/ActionButton';
import Collapse from '../../Icons/Colapse/Collapse';
import AttributeEditor from '../AttributeEditor/AttributeEditor';

const AttributeGroup = ({
  attributes,
  groupTitle,
  groupedAttributes,
}: {
  attributes: AttributeVm[];
  groupTitle: string;
  groupedAttributes?: attributeGroup[];
}) => {
  const [isCreateActive, setIsCreateActive] = useState(false);
  const [isActive, setIsActive] = useState(attributes.length === 1);
  const { reorderAttr, reorderAttrGroup } = useAttributeOrderer({ attributes, groupedAttributes });

  if (attributes.length === 1 && !isCreateActive) {
    return (
      <AttributeEditor
        attribute={attributes[0]}
        reorderAttr={(attr, dir) => reorderAttrGroup(groupTitle, dir)}
      />
    );
  }

  const newAttr: AttributeVm = attributes?.[0]
    ? {
        profileId: attributes[0].profileId,
        sectionId: attributes[0].sectionId,
        type: attributes[0].type,
        typeDefinition: attributes[0].typeDefinition,
        acl: { requiredSecurityGroup: SecurityGroupType.Owner },
        id: undefined,
        priority: attributes[0].priority,
        data: {},
      }
    : undefined;

  return (
    <div
      className={`relative my-10 overflow-x-hidden ${
        !isActive ? 'cursor-pointer transition-transform' : ''
      }`}
      style={{ paddingBottom: `${attributes.length * 10}px` }}
      onClick={() => {
        if (!isActive) setIsActive(true);
      }}
    >
      <div className="flex flex-row justify-between">
        <h2 onClick={() => setIsActive(false)} className={'cursor-pointer text-2xl'}>
          <Collapse className={`inline-block h-4 w-4 ${isActive ? 'opacity-100' : 'opacity-0'}`} />{' '}
          {groupTitle} <small>({attributes.length})</small>
        </h2>

        <ActionButton
          onClick={() => setIsCreateActive(true)}
          className={`my-auto ${isActive ? '' : 'pointer-events-none opacity-50 grayscale'}`}
          icon="plus"
          type="mute"
        >
          {t('Add another')}
        </ActionButton>
      </div>
      <div
        className={`mt-5 border-l-[16px] border-slate-50 transition-transform dark:border-slate-600 ${
          isActive ? 'pl-5' : '-translate-x-4 hover:translate-x-0'
        }`}
      >
        {isCreateActive && (
          <span key={'new'} className="bg-pink-600">
            <AttributeEditor
              attribute={newAttr}
              onSave={() => setIsCreateActive(false)}
              doRemove={() => setIsCreateActive(false)}
            />
          </span>
        )}
        {attributes.map((attr, index) => {
          return (
            <span
              key={attr.id}
              className={
                !isActive && index !== 0
                  ? `absolute left-0 right-0 top-0 bg-white shadow-slate-50 dark:bg-slate-800`
                  : ''
              }
              style={{ transform: `translateX(${index * 4}px) translateY(${index * 10}px)` }}
            >
              <AttributeEditor
                attribute={attr}
                className={
                  !isActive ? 'pointer-events-none my-0 opacity-50 grayscale' : 'mt-0 mb-5'
                }
                reorderAttr={reorderAttr}
              />
            </span>
          );
        })}
      </div>
    </div>
  );
};

export default AttributeGroup;
