import { AccessControlList, SecurityGroupType } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { t } from '../../helpers/i18n/dictionary';
import AclDialog from '../Dialog/AclDialog/AclDialog';
import Lock from '../Icons/Lock/Lock';
import OpenLock from '../Icons/OpenLock/OpenLock';

export const getAclSummary = (acl: AccessControlList) => {
  return acl.requiredSecurityGroup === SecurityGroupType.Anonymous
    ? t('Public')
    : acl.requiredSecurityGroup === SecurityGroupType.Authenticated
    ? t('Authenticated')
    : acl.requiredSecurityGroup === SecurityGroupType.Connected
    ? t('Connections')
    : acl.requiredSecurityGroup === SecurityGroupType.Owner
    ? t('Owner')
    : t('Owner');
};

const AclEditor = ({
  acl,
  onChange,
  className,
}: {
  acl: AccessControlList;
  onChange?: (acl: AccessControlList) => void;
  className?: string;
}) => {
  const [isEditing, setIsEditing] = useState(false);

  const Icon = (props: { className: string }) => {
    return acl.requiredSecurityGroup === SecurityGroupType.Anonymous ||
      acl.requiredSecurityGroup === SecurityGroupType.Authenticated ? (
      <OpenLock {...props} />
    ) : (
      <Lock {...props} />
    );
  };

  return (
    <>
      <button
        title={acl.requiredSecurityGroup}
        className={`mr-2 inline-block ${onChange ? '' : 'cursor-default'} ${className ?? ''}`}
        onClick={() => onChange && setIsEditing(true)}
      >
        <Icon className="h-5 w-5" />
      </button>
      <AclDialog
        acl={acl}
        isOpen={isEditing}
        title={t('Edit security')}
        onCancel={() => {
          setIsEditing(false);
        }}
        onConfirm={(newAcl) => {
          onChange(newAcl);
          setIsEditing(false);
        }}
      />
    </>
  );
};

export default AclEditor;
