import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { ApiType, AttributeFile, Client } from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';
import useStaticFiles from '../staticFiles/useStaticFiles';
import { AttributeVm } from './useAttributes';

const useAttribute = ({ profileId, attributeId }: { profileId?: string; attributeId?: string }) => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const queryClient = useQueryClient();
  const { mutate: publishStaticFiles } = useStaticFiles().publish;

  const fetchData = async (profileId?: string, attributeId?: string) => {
    if (!profileId || !attributeId) {
      return;
    }
    const foundAttribute = await client.profileDataProvider.getAttribute(profileId, attributeId);

    return foundAttribute;
  };

  const saveData = async (attribute: AttributeFile) => {
    return await client.profileDataProvider.saveAttribute(attribute);
  };

  const removeData = async (attribute: AttributeFile) => {
    if (attribute.fileId) {
      return await client.profileDataProvider.removeAttribute(
        attribute.profileId,
        attribute.fileId
      );
    } else {
      console.log('error...');
    }
  };

  return {
    fetch: useQuery(
      ['attribute', profileId, attributeId],
      () => fetchData(profileId, attributeId),
      {
        refetchOnWindowFocus: false,
      }
    ),
    save: useMutation(saveData, {
      onMutate: async (newAttr) => {
        await queryClient.cancelQueries(['attribute', newAttr.profileId, newAttr.id]);

        // Update single attribute
        const previousAttr = queryClient.getQueryData(['attribute', newAttr.profileId, newAttr.id]);
        queryClient.setQueryData(['attribute', newAttr.profileId, newAttr.id], newAttr);

        // Update section attributes
        const previousAttributes: AttributeVm[] = queryClient.getQueryData([
          'attributes',
          newAttr.profileId,
          newAttr.sectionId,
        ]);

        // if new attribute doesn't have an id, then it's a new one, so can't update but need to add
        // Sorting happens here, as it otherwise happens within the provider
        const newAttributes = (
          newAttr.id
            ? previousAttributes?.map((attr) => (attr.id === newAttr.id ? newAttr : attr))
            : [newAttr, ...previousAttributes]
        )?.sort((a, b) => a.priority - b.priority);

        queryClient.setQueryData(
          ['attributes', newAttr.profileId, newAttr.sectionId],
          newAttributes
        );

        return { previousAttr, newAttr, previousAttributes };
      },
      onError: (err, newAttr, context) => {
        console.error(err);

        // Revert local caches to what they were
        queryClient.setQueryData(
          ['attribute', newAttr.profileId, context.newAttr.id],
          context.previousAttr
        );
        queryClient.setQueryData(
          ['attributes', newAttr.profileId, newAttr.sectionId],
          context.previousAttributes
        );
      },
      onSettled: (newAttr) => {
        if (newAttr.id) {
          queryClient.invalidateQueries(['attribute', newAttr.profileId, newAttr.id]);
        } else {
          queryClient.invalidateQueries(['attribute']);
        }

        queryClient.invalidateQueries(['attributes', newAttr.profileId, newAttr.sectionId]);
      },
      onSuccess: () => {
        publishStaticFiles();
      },
    }),
    remove: useMutation(removeData, {
      onMutate: async (toRemoveAttr) => {
        await queryClient.cancelQueries([
          'attributes',
          toRemoveAttr.profileId,
          toRemoveAttr.sectionId,
        ]);

        // Update section attributes
        const previousAttributes: AttributeVm[] = queryClient.getQueryData([
          'attributes',
          toRemoveAttr.profileId,
          toRemoveAttr.sectionId,
        ]);
        const updatedAttributes = previousAttributes.filter((attr) => attr.id !== toRemoveAttr.id);
        queryClient.setQueryData(
          ['attributes', toRemoveAttr.profileId, toRemoveAttr.sectionId],
          updatedAttributes
        );

        return { toRemoveAttr, previousAttributes };
      },
      onError: (err, toRemoveAttr, context) => {
        console.error(err);

        // Revert local caches to what they were
        queryClient.setQueryData(
          ['attributes', toRemoveAttr.profileId, toRemoveAttr.sectionId],
          context.previousAttributes
        );
      },
      onSettled: (_data, _err, variables) => {
        queryClient.invalidateQueries(['attributes', variables.profileId, variables.sectionId]);

        publishStaticFiles();
      },
    }),
  };
};

export default useAttribute;
